<template>
  <el-container class="home_container">
    <el-header>
      <div class="home_title">应急系统</div>
      <div class="home_userinfoContainer">
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link home_userinfo">
            {{ currentUserName
            }}<i class="el-icon-arrow-down el-icon--right home_userinfo"></i>
          </span>
          <el-dropdown-menu>
            <el-dropdown-item command="sysCfg">系统管理</el-dropdown-item>
            <el-dropdown-item command="logout" divided
              >退出登录</el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <el-container>
      <el-aside width="160px" style="">
        <el-menu
          default-active="0"
          class="el-menu-vertical-demo"
          style="background-color: #ececec"
          router
          @open="handleOpen"
          @close="handleClose"
        >
          <template>
            <el-menu-item :index="'/layout'">
              <!-- <i :class="el-icon-map-location"></i> -->
              <span>home</span>
            </el-menu-item>
          </template>
          <template>
            <el-menu-item :index="'/layout/data'">
              <!-- <i class="el-icon-menu"></i> -->
              <span>data</span>
            </el-menu-item>
          </template>
          <template>
            <el-menu-item :index="'/layout/equipment'">
              <!-- <i class="el-icon-menu"></i> -->
              <span>equipment</span>
            </el-menu-item>
          </template>
        </el-menu>
      </el-aside>
      <el-main style=" padding-top: 10px; padding-left: 4px; padding-right: 4px">
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
import {mapActions} from 'vuex';
export default {
  data() {
    return {
      currentUserName: "系统管理员",
      isAdmin: true,
      isNormal: true,
      hasUncheckedApplyPermission: false,
      hasUncheckedApprovalPermission: false,
      hasTagExceptionPermission: false,
    };
  },
  methods: {
    handleCommand(command) {
      var _this = this;
      if (command == "logout") {
        this.$confirm("注销登录吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(function () {
          _this.currentUserName = "游客";
          _this.$router.replace({ path: "/" });
        });
      }
    },
    ...mapActions([
      'select_line_data',
      'select_tray_data'
    ])
  },
  created() {
    this.select_line_data(localStorage.getItem('token'))
    this.select_tray_data(localStorage.getItem('token'))
  },
  mounted: function () {
    var _this = this;
    _this.currentUserName = "系统管理员";
  },
};
</script>
<style scoped>

.home_container {
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
}

.el-header {
  background-color: #20a0ff;
  color: #333;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.el-aside {
  background-color: #ececec;
}

.el-main {
  background-color: #fff;
  color: #000;
}

.home_title {
  color: #fff;
  font-size: 22px;
  display: inline;
}

.home_userinfo {
  color: #fff;
  cursor: pointer;
}

.home_userinfoContainer {
  display: inline;
  margin-right: 20px;
}
</style>

